*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App{
    width: 800px;

}

.post{
    display: flex;
    padding: 15px;
    border: 2px solid crimson;
    margin-top: 15px;
    justify-content: space-between;
    align-posts: center;
}
.post__btns{
    display: flex;

   align-items: center;

}


.page__wrapper{
    margin-top: 30px;
    display: flex;

}
.page{
    padding: 10px;
    border: 1px solid crimson;
    cursor: pointer;
}

.page__current{

    border: 2px solid black;
   font-weight: bold;
}

.navbar{
    height: 50px;
    width: 100vw;
    display: flex;
    align-items: center;
    padding: 0 25px;

    background-color: aquamarine;
}
.navbar__links{

    margin-left: auto;

}

