.postik-enter{
    -webkit-transform: translateX(-150px);
            transform: translateX(-150px);
}

.postik-enter-active{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition:all 500ms ease-in;
}

.postik-exit{
    opacity: 1;
}
.postik-exit-active{
    -webkit-transform: translateX(-150px);
            transform: translateX(-150px);
    transition: all 500ms ease-in;
}
.Loader_loader__GL267{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 3px dashed teal;
    -webkit-animation: Loader_rotate__Vu5Ay 5s infinite linear;
            animation: Loader_rotate__Vu5Ay 5s infinite linear;
}

@-webkit-keyframes Loader_rotate__Vu5Ay {
    from{
    -webkit-transform: rotate(0deg) scale(1);
            transform: rotate(0deg) scale(1);
    }
    to{
        -webkit-transform: rotate(360deg) scale(0);
                transform: rotate(360deg) scale(0);
    }

}

@keyframes Loader_rotate__Vu5Ay {
    from{
    -webkit-transform: rotate(0deg) scale(1);
            transform: rotate(0deg) scale(1);
    }
    to{
        -webkit-transform: rotate(360deg) scale(0);
                transform: rotate(360deg) scale(0);
    }

}
